.app {
    font-family: Arial, sans-serif;
    padding: 20px;
  }
  
  .login-container,
  .signup-container {
    max-width: 300px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  input,
  button,
  select {
    margin-bottom: 10px;
    padding: 10px;
    font-size: 16px;
  }
  
  .dashboard {
    max-width: 600px;
    margin: 0 auto;
  }
  
  .add-task {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .add-task input,
  .add-task select,
  .add-task button {
    margin-bottom: 10px;
    padding: 10px;
    font-size: 16px;
  }
  
  .task-list {
    margin-top: 20px;
  }
  
  .task-item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .task-item input,
  .task-item select {
    margin: 0 10px;
  }
  
  .task-item.urgent {
    border-color: red;
  }
  
  .task-item.medium {
    border-color: orange;
  }
  
  .task-item.low {
    border-color: green;
  }
  